body {
  margin: 0;
  padding: 0 2%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*code {font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;}*/

h1, h2, h3 {margin:.25em;}

.nav-link:hover {background-color: #c0c0c0;text-decoration: underline;}

.MainHeader {width:100%;height:3.5em;padding:.5em 0;background-color:#c0c0c0;color:black;}

.MMSLogo {width:48px;height:48px;float: left;}

.HeaderText {width: auto;height: 2em;float: left;padding: 0;}

.ToRight {float: right;padding-right: 4px;}

.MainBody {width:100%;height:80vh;overflow:auto;}

.Footer {width: 100%;background-color: #c0c0c0;}

.ActionButton {background-color: lightblue;color:black;}

.downright {margin-right: 5px;margin-bottom: 1em;}

.boxed {float:left; padding:4px; margin:4px;}

.productbox {height: 3em;clear:both;}
.productnamebox {width: 400px;}

